import React, { Component, Suspense } from "react";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import styled, { ThemeProvider } from "styled-components";
import axios from "axios";
import LoadingSpinner, { LoadingContainer } from "./common/loading";
import Helmet from "react-helmet";
import { ToastContainer, toast } from "react-toastify";

const ContactForm = React.lazy(() => import("./common/contact-form"));

const SliderContainer = styled.div`
  & img {
    width: inherit;
    background-image: url("/img/pulse.gif");
    background-repeat: no-repeat;
    background-position: center;
  }
`;

const DetailsList = styled.ul`
  list-style-type: none;
  padding-left: 0px;
  color: ${(props) => props.theme.primaryText};
`;

const H1 = styled.h1`
  color: ${(props) => props.theme.primaryText};
`;

const Error404 = styled.div`
  margin: 50px auto;
  color: ${(props) => props.theme.bgColor};
  -webkit-filter: invert(100%);
  filter: invert(100%);
  font-size: 40px;
  text-align: center;

  & span {
    font-size: 1.4em;
    border-bottom: 3px solid red;
  }
`;

class CarDetails extends Component {
  state = {};

  constructor(props) {
    super(props);

    this.primaryRef = React.createRef();
    this.secundaryRef = React.createRef();
  }

  renderSlides(car) {
    const items = [];

    for (let i = 1; i <= car.photos; i++) {
      items.push(
        <SplideSlide key={`${car.id}/${i}`}>
          <img
            src={process.env.PUBLIC_URL + `/img/${car.id}/${i}.webp`}
            alt={"Fotos da viatura"}
          />
        </SplideSlide>
      );
    }

    return items;
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.primaryRef.current !== null) {
      this.primaryRef.current.sync(this.secundaryRef.current.splide);
    }
  }

  async componentDidMount() {
    // Set the sync target right after the component is mounted.
    if (this.primaryRef.current !== null) {
      this.primaryRef.current.sync(this.secundaryRef.current.splide);
    }

    const { data: cars } = await axios.get(
      "https://vinculobjetivobe.vercel.app/cars"
    );

    if (cars) {
      const car = cars.find(
        (c) => c.id.toString() === this.props.match.params.id
      );
      document.title = car.title;
    }

    this.setState({ cars });
  }

  GetCarSpecs(car) {
    return Object.keys(car.attributes).map((key) => {
      return (
        <li key={key}>
          <strong>{car.attributes[key].description}:</strong>{" "}
          {car.attributes[key].value}
        </li>
      );
    });
  }

  render() {
    const primaryOptions = {
      type: "loop",
      perPage: 1,
      perMove: 1,
      gap: "1rem",
      pagination: true,
      lazyLoad: "nearby",
    };

    const secondaryOptions = {
      type: "slide",
      rewind: true,
      gap: "1rem",
      pagination: false,
      fixedWidth: 110,
      fixedHeight: 70,
      cover: true,
      focus: "center",
      isNavigation: true,
      updateOnMove: true,
    };

    const structuredJSON = (car) => {
      let data = {
        "@context": "http://schema.org/",
        "@type": "Product",
        name: `${car.title}`,
        image: `${
          "https://vinculobjetivo.pt" + `/img/${car.id}/thumbnail.webp`
        }`,
        description: `${car.title} | ${car.attributes.kms.value} KMS | ${car.attributes.year.value} | ${car.attributes.fuelType.value}`,
        url: `https://vinculobjetivo.pt/carros`,
        offers: {
          "@type": "Offer",
          priceCurrency: "‚Ç¨",
          price: car.price,
          url: `https://vinculobjetivo.pt/carro/${car.id}`,
          itemCondition: "https://schema.org/UsedCondition",
          availability: `${
            car.sold
              ? "https://schema.org/SoldOut"
              : "https://schema.org/InStock"
          }`,
          seller: {
            "@type": "Organization",
            name: "Vinculobjetivo",
          },
        },
        brand: {
          "@type": "Brand",
          name: `${car.attributes.brand.value}`,
        },
      };

      return JSON.stringify(data);
    };

    const { cars } = this.state;

    if (!cars) {
      return (
        <LoadingContainer>
          <LoadingSpinner></LoadingSpinner>
        </LoadingContainer>
      );
    }

    const { id } = this.props.match.params;
    const car = cars.find((c) => c.id.toString() === id);

    if (car && car.sold) {
      var options = {
        position: "top-right",
        autoClose: false,
        hideProgressBar: true,
        closeOnClick: false,
        pauseOnHover: false,
        draggable: false,
        progress: undefined,
      };

      toast.error("‚ö†Ô∏è Esta viatura j√° foi vendida üò¢", options);
    }

    if (!car || id > cars.length) {
      return (
        <ThemeProvider theme={this.props.theme}>
          <Error404>
            <span>Erro:</span> ID da viatura n√£o existe: {id}
          </Error404>
        </ThemeProvider>
      );
    }

    return (
      <Suspense
        fallback={
          <LoadingContainer>
            <LoadingSpinner></LoadingSpinner>
          </LoadingContainer>
        }
      >
        <ToastContainer limit={1} />
        <ThemeProvider theme={this.props.theme}>
          <Helmet>
            <script className="structured-data-list" type="application/ld+json">
              {structuredJSON(car)}
            </script>
          </Helmet>

          <div className="row ml-lg-5 mr-lg-5 ml-sm-2 mr-sm-2 ml-1 mr-1">
            <div className="col-12 col-lg-9 mt-5">
              {car.sold ? (
                <h2>
                  <span class="badge badge-warning">Vendido</span>
                </h2>
              ) : (
                ""
              )}
              <SliderContainer>
                <Splide options={primaryOptions} ref={this.primaryRef}>
                  {this.renderSlides(car)}
                </Splide>
                <Splide
                  options={secondaryOptions}
                  ref={this.secundaryRef}
                  className="mt-3"
                >
                  {this.renderSlides(car)}
                </Splide>
              </SliderContainer>
            </div>
            <div className="col-12 col-lg-3 mt-5">
              <H1>{car.title}</H1>
              <DetailsList>{this.GetCarSpecs(car)}</DetailsList>
            </div>
          </div>

          <ContactForm
            theme={this.props.theme}
            subject={car.title}
          ></ContactForm>
        </ThemeProvider>
      </Suspense>
    );
  }
}

export default CarDetails;
